import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { cn } from '@/lib/utils';
import { useEffect, useState } from 'react';
import AdvertisingGroup from '../components/marketing/advertisingGroup/AdvertisingGroup';
import CampaignManagement from '../components/marketing/campaignManagement/CampaignManagement';
import IndividualAds from '../components/marketing/individualAds/IndividualAds';

export default function MarketingPage() {
    const [activeSection, setActiveSection] = useState(1);

    const searchParams = new URLSearchParams(window.location.search);
    const loans = searchParams.get('loans');

    const handleSelect = (value: number) => {
        setActiveSection(value);
    };

    useEffect(() => {
        if (loans) {
            setActiveSection(3);
        }
    }, [loans]);

    return (
        <div className="w-full mt-6">
            <div className="mb-[21px]">
                <UnderlineHeading text="إدارة التسويق" />
            </div>
            <div className="w-full flex flex-row items-center gap-[1px]">
                <button
                    onClick={() => handleSelect(1)}
                    className={cn(
                        'w-full transition-colors text-base py-3 text-white-blue-50 font-bold leading-[120%] tracking-normal rounded-s-lg',
                        {
                            'bg-primery-blue-700': activeSection === 1,
                            'bg-primery-blue-400': activeSection !== 1,
                        },
                    )}
                >
                    إدارة الحملات
                </button>
                <button
                    onClick={() => handleSelect(2)}
                    className={cn(
                        'w-full transition-colors text-base py-3 text-white-blue-50 font-bold leading-[120%] tracking-normal',
                        {
                            'bg-primery-blue-700': activeSection === 2,
                            'bg-primery-blue-400': activeSection !== 2,
                        },
                    )}
                >
                    مجموعة الاعلانات
                </button>
                <button
                    onClick={() => handleSelect(3)}
                    className={cn(
                        'w-full transition-colors text-base py-3 text-white-blue-50 font-bold leading-[120%] tracking-normal',
                        {
                            'bg-primery-blue-700': activeSection === 3,
                            'bg-primery-blue-400': activeSection !== 3,
                        },
                    )}
                >
                    الإعلانات الفردية
                </button>
            </div>
            {activeSection === 1 && <CampaignManagement />}
            {activeSection === 2 && <AdvertisingGroup />}
            {activeSection === 3 && <IndividualAds />}
        </div>
    );
}
