import axios from 'axios';

const campaignFields =
    'id,name,objective,account_id,buying_type,daily_budget,lifetime_budget,spend_cap,bid_strategy,pacing_type,status,effective_status,promoted_object,recommendations,start_time,stop_time,created_time,updated_time,adlabels,issues_info,special_ad_categories,special_ad_category_country,smart_promotion_type,is_skadnetwork_attribution,adsets{id,name,targeting,ads{id,name,creative},insights{impressions,clicks,spend}}';

const accountId = '1103322178357172';
const accessToken =
    'EAAKmF37Cb88BPC2b0yIrDxsUwaJZBzHMn1poSNqucubdykZAl4cCZBobbX6NCggbwpXSQYLCaMjZAlCdO8xoMNJylnqxSsyZCcZCOTnsxVXNr0ZAP1n8YbRAmHZAwvcToIwhODrWkj1cPQp1f0U5ZBdY9hOhvji0QMuzunMJBhzEM3iZBRZBce4ZCmclt1aGxt5Hmf5H1b8TKf9T';

export default function CampaignManagementList() {
    const [data, setData] = useState<Loan[]>([]);
    const [pageCount, setPageCount] = useState(0);
    const [totalCount, setTotalCount] = useState(0);
    const [isLoading, setIsLoading] = useState(false);

    // Use useCallback to ensure the function reference is stable
    const fetchData = useCallback(async (options: FetchDataOptions) => {
        setIsLoading(true);
        try {
            // const response = await apiGet({
            //     endpoint: endpoints.loan.list,
            //     queryParams: { page: options.pageIndex + 1, ...options.filters, search: options.globalFilter },
            // });
            const response = await axios.get(
                `https://graph.facebook.com/v20.0/act_${accountId}/campaigns?fields=${campaignFields}&access_token=${accessToken}`,
            );
            console.log(response.data);
            setData(response.data.data);
            setPageCount(Math.ceil(response.data.data.length / 10));
            setTotalCount(response.data.data.length);
        } catch (error) {
            console.error('Error fetching data:', error);
        } finally {
            setIsLoading(false);
        }
    }, []);
    return (
        <div className="mt-6 mb-20 rounded-lg border border-[#E8E7EA] shadow-[0px_4px_4px_-1px_#0C0C0D0D,0px_4px_4px_-1px_#0C0C0D1A]">
            <DataTable
                columns={columns}
                data={data}
                pageCount={pageCount}
                totalCount={totalCount}
                onFetchData={fetchData}
                DataTableToolbar={DataTableToolbar}
                isLoading={isLoading}
            />
        </div>
    );
}
