import StatisticsCards from '@/components/cards/StatisticsCards';
import DollarCircleIcon from '@/components/icons/marketing/DollarCircleIcon';
import EyeIcon from '@/components/icons/marketing/EyeIcon';
import MouseSquareIcon from '@/components/icons/marketing/MouseSquareIcon';
import TickCircleIcon from '@/components/icons/marketing/TickCircleIcon';
import { employeesStats } from '@/features/humanResources/types/employeesStats.types';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import { Card } from '@/types/statistics';
import { useEffect, useState } from 'react';

export default function IndividualAdsStatistics() {
    const [stats, setStats] = useState<employeesStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.employee.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'اعلانات قيد التشغيل', num: stats?.total_employees, percent: 12, icon: <TickCircleIcon /> },
        { text: 'اجمالي النقرات', num: 35, percent: 0, icon: <MouseSquareIcon /> },
        { text: 'اجمالي المشاهدات', num: stats?.total_full_time, percent: -0.3, icon: <EyeIcon /> },
        { text: 'اجمالي المبلغ المنفق', num: stats?.total_part_time, percent: -0.3, icon: <DollarCircleIcon /> },
    ];

    return <StatisticsCards cards={cards} numJustifyStart={true} />;
}
